.ApplicationList {
    position: relative;
    -ms-flex: 0 0 240px;
        flex: 0 0 240px;
}
.ApplicationList button {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    display: block;
    width: 100%;
    height: 48px;
    padding: 0;
    margin: 0;
    border: 0;
    border-radius: 3px;

    background: var(--purple-02);
    color: white;
    font-family: 'Muli', sans-serif;
    font-size: 16px;
    text-align: left;
    cursor: pointer;
}

.ApplicationList button:hover {
    background: var(--purple-03);
}

.ApplicationList__list {
    list-style: none;
    position: absolute;
    width: 240px;
    top: 100%;
    left: 0;
    padding: 0;
    margin: 0;

    border-radius: 3px;
    background: var(--purple-02);
    -webkit-box-shadow: 0 3px 10px rgba(0, 0, 0, 0.3);
            box-shadow: 0 3px 10px rgba(0, 0, 0, 0.3);

    opacity: 0;
    pointer-events: none;
    -webkit-transition: opacity 0.1s;
    -o-transition: opacity 0.1s;
    transition: opacity 0.1s;
}
.ApplicationList__list.--visible {
    opacity: 1;
    pointer-events: auto;
    -webkit-transition: opacity 0.2s;
    -o-transition: opacity 0.2s;
    transition: opacity 0.2s;
}

.ApplicationList__list li {
    margin: 0;
}

.ApplicationList__list li.--selected,
.ApplicationList__list li:hover {
    background: var(--purple-03);
}

.ApplicationList__item {
    padding: 8px;
}
.ApplicationList__item b {
    display: block;
    font-size: 14px;
    line-height: 22px;
    white-space: nowrap;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

.ApplicationList__item.--enabled b:after {
    content: '';
    display: inline-block;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: var(--green-01);
    -webkit-box-shadow: 0 0 0px 1px #77ffa7, 0 0 0px 2px rgba(0, 0, 0, 0.5);
            box-shadow: 0 0 0px 1px #77ffa7, 0 0 0px 2px rgba(0, 0, 0, 0.5);
    margin-left: 8px;
}

.ApplicationList__item span {
    display: block;
    font-size: 10px;
    line-height: 1;
    color: rgba(255, 255, 255, 0.5);
    text-transform: uppercase;
}



